<template>

 <div class="app-container">
     <!-- 1.公共顶部导航条 -->
     <header class="mui-bar mui-bar-nav" style="background:#fff;">
        <!--   <a href=" " class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a> -->
            
            <!-- <img class="tou" @click="tou" src="../src/img/01l.png" style="background:#fff;">
            <img class="tou" @click="tou1" src="../src/img/02r.png" style="background:#fff;"> -->
            <img class="san" @click="san" src="../src/img/san.png" alt="">
            <div class="app-da" :class="active==1?'show':'hide'" >
                <ul>
                    <li @click="home">首页</li>
                    <!-- <li @click="about">关于我们</li>
                    <li @click="news">新闻资讯</li>
                    <li @click="product">产品展示</li> -->
                    <li @click="join">招商加盟</li>
                </ul>
            </div>
      </header> 
     <!-- <mt-header fixed title="">
         <div class="img"><img src="../src/img/1515997569.png" alt=""></div>
    </mt-header> -->
    <div class="app-header">
        <div class="app-img">
            <img src="../src/img/1515997569.png" alt="">
        </div>
    </div>
     <!-- 2.容器显示其他组件 -->
    <router-view></router-view>
    <!-- 底部tabbar -->
    <nav class="mui-bar mui-bar-tab">
        <div class="mui-tab-item" @click="foot1">
			<router-link to="Home" class="mui-tab-item" :class="isa==1?'mui-active':''" href="#tabbar">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label">首页</span>
			</router-link></div>
            <div class="mui-tab-item"  @click="foot2">
			<router-link to="gwc" :class="isa==2?'mui-active':''" class="mui-tab-item" href="#tabbar-with-contact">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</router-link></div>
			<div class="mui-tab-item" @click="foot3">
            <router-link to="my" :class="isa==3?'mui-active':''" class="mui-tab-item" href="#tabbar-with-chat">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">个人中心</span>
			</router-link></div>
	</nav>
 </div>
</template>
<script>
export default {
    data(){
        return{
            active:0,
            isa:1,
            isb:true,
        }
    },
     methods:{
         san(){
             if(this.active==1){
                 this.active=0;
             }else{
                 this.active=1;
             }
         },
         foot1(){
            //  console.log(111);
             this.isa=1;
         },
          foot2(){
              
             this.isa=2;
         },
          foot3(){
             this.isa=3;
         },
         tou(){
            //  if()
             this.$router.go(-1);
         },
         tou1(){
             this.$router.go(1);
         },
         home(){
            this.$router.push("/Home");
            this.active=0;
         },
         join(e){
            var id = e.target.dataset.lid;
            var path = "/Procedure?id=1";
            this.$router.push(path);
            this.active=0;
         },
         
     }
}
</script>

<style>
.mui-active{
        color: #007aff!important;
}
/* .db{color: #007aff;} */
.app-container header{
 text-align: right
}
.app-container header>img{
    margin-top: 10px;
    /* background: #fff!important; */
}
.app-da ul{
    width: 100%!important;
    height: 160px!important;
    text-align: center;
    color: #7c7a7a;
    /* border: 1px solid #000; */
    background: #f5f5f5;
    line-height: 30px;
    margin: 0;
   
}
header.mui-bar.mui-bar-nav{padding: 0;}
 .app-img{background: #fff;padding: 0px}

.app-container{padding-top:0!important ;}
   .app-img img{
       width: 150px;
       height: 40px;
       
   }
   .app-container{
     padding-top:10px;
     padding-bottom:50px;
     overflow-x:hidden;

   } 
</style>



