<template>
<div class="app-gwc">
				<!-- <div class="mui-card-header">-->
                    <!-- 添加复选框  为复选框绑定单击事件-->
                    <!-- 全选 -->
                    <!-- <input type="checkbox" @click="selectAll" :checked="allcb">
                </div> --> 
				<!-- <div class="mui-card-content">
                    <input type="checkbox" @click="selectAll" :checked="allcb">全选
				</div> -->
   <ul class="mui-table-view">
        <li class="mui-table-view-cell mui-media" v-for="(item,i) of list" :key=i>
             <!-- 添加复选框 -->
            <input type="checkbox" :data-id="item.id" :checked="item.cb" :data-i="i" @click="modifyItem">
            <a href="javascript:;">
                <img class="mui-media-object mui-pull-left" :src="item.img">
                 <!-- <input name="checkbox1" value="Item 1" type="checkbox" checked> -->
                <div class="mui-media-body" >
                    <span class='mui-ellipsis'>
                        <!-- 商品名 -->
                        {{item.nname}}
                    </span>
                    <span>
                        ¥{{item.pice}}
                        
                    </span>
                    
                </div>
                <div class="mui-numbox" style="margin-left:15px;">				
                    <button @click="minus(i,item.id)" class="mui-btn mui-btn-numbox-minus" type="button">-</button>
					<span>{{item.geshu}}</span>
					<button @click="plus(i,item.id)" class="mui-btn mui-btn-numbox-plus" type="button">+</button>
				</div>
                <span><input type="button" value="删除" :data-id="item.id" @click="removeItem" :data-idx="i"></span>
            </a>
            
        </li>
        
    </ul> 
    <div class="di">
        <div>
            <input type="checkbox" @click="selectAll" :checked="allcb">
            <span>全选</span>
        </div>
        <div>
            <div>总价:{{zongjia}}</div>
        </div>
        <div>
            <button>结算</button>
        </div>
    </div>
</div>
</template>

<script>
import {Toast} from 'mint-ui'
export default {
    data(){
        return{
           list:[],//购物车列表
            allcb:false,//全选状态  默认被未选中
            zongjia:'',s:0,
            tmp:1,
        }
        
    },
     created(){
       this.loadMore();
    },
    methods:{
         //全选复选框
        selectAll(e){
            //1.获取全选按钮选中装态
            var rs = e.target.checked;
            //2.遍历数据所有元素
            for(var item of this.list){
                //3.item.cb = 全选状态
                item.cb = rs;
            }
            //4.修改全选数据状态
            this.allcb = rs;
            if(rs==true){//如果
                var s=this.s;
                var a=this.list.length;
                for(var i=0;i<a;i++){
                    s += this.list[i].pice*this.list[i].geshu;
                }
                this.zongjia=s;
            }

            if(rs!=true){
                var s=this.s;
                var a=this.list.length;
                for(var i=0;i<a;i++){
                    s = 0;
                }
                this.zongjia=s;
            }
           
        },
        //修改复选框状态 选中 取消
        modifyItem(e){
            //1.修改当前元素选中 状态 true false
            var rs = e.target.checked;
            // console.log(rs);
            //2.当前元素在数组中 下标
            var i = e.target.dataset.i;
            // console.log(rs+":"+i);
            //3.将状态赋值当前数组下标中 对象cb值
            this.list[i].cb = rs;
            // console.log(this.list);
            //判断全选复选框状态 true or false
            //1.获取数组个数
            var size1 = this.list.length;
            //2.计算数组中cb true 元素个数
            var count = 0;
            for(var item of this.list){
                if(item.cb){
                    count++;
                }
            }
            // console.log(size1+":"+count);
            //3.数组个数==cb true 个数[累加和]
            if(count==size1){//选中个数和总数相同 全选框为true 全选
                //4.件allcb = true
                this.allcb = true;
            }else{
                //5.否则allcb = false
                this.allcb = false;//全选框为false
            }
            var rs = e.target.checked;
            var s=this.s;
            // console.log(this.s);
            if(rs==true){
                s += this.list[i].pice*this.list[i].geshu;
                this.s=s;
                this.zongjia=s;
            }
            if(rs!=true){
                 s -= this.list[i].pice*this.list[i].geshu;
                this.s=s;
                this.zongjia=s;
            }
            
        },
         //删除选中多个商品       
        delItem(){
            // console.log("删除选中商品...");
            // [{cb:true,id:1},{cb:true,id=3}] ===选中1,3
            //1.创建空字符串
            var html="";
            //2.遍历数据中元素
            for(var item of this.list){
                //3.判断cb==true
                if(item.cb){
                    //4.保存id拼接字符串"1,6,7"
                    html += item.id+",";
                }
            }
            //判断选中个数   如果未选中则出现提示
            if(html.length==0){
                Toast("请选中您需要删除商品!");
                return;
            }
            // console.log(html);
            //5.截取字符串去除最后, 1,6,7,=>1,6,7
            //substring(?,?)
            //?截取起始下标
            //?截取几个字符
            html = html.substring(0,html.length-1);
            // console.log(html);
            // return;
            //6.发送ajax请求 ids=1,6,7
            var url = "removeMItem?ids="+html;
            this.axios.get(url).then(result=>{
                //7.获取返回数据提示  删除成功或失败
                if(result.data.code==1){
                    Toast("删除成功");
                    this.loadMore();
                }else{
                    Toast("删除失败");
                }
            }) 
        },
        //删除购物车商品
        removeItem(e){
            //1.获取购物车商品 id
            var id=e.target.dataset.id;
             //1.获取下标
            var idx=e.target.dataset.idx;
            // console.log(id);
            //2.创建url
            var url="removeItem";
            //创建内容
           var postData = this.qs.stringify({
               id:id
           });
           //3.发送ajax请求
           this.axios.post(url,postData).then(result=>{
            if(result.data.code==1){
                //删除对应的数据中值
                //1.获取下标           
                //2.依据下标删除指定数组商品
                //删除数组中对应下标商品
                //splice (数组下标，个数)
                this.list.splice(idx,1);
                Toast("删除成功");
            }else{
                Toast("删除失败");
            }
           })
            //4.获取返会结果
            //5.提示删除结果
            //6.list对应商品项删除  
        },
        loadMore(){
            var ss=localStorage.getItem('una');
            if(!ss){//判断现在是否是登录状态
                return Toast("请登录")
            }
            var url="gwc1?uname="+ss;
            //2.发送ajax请求 
            this.axios.get(url).then(result=>{
              
                 //3.获取返回数据
                var rows = result.data;
                //7.为数组中每个对象添加属性cb初始值false
                // for(var item of rows){
                //     item.cb = false;
                // }
                this.list = rows;
            })
        },
        minus(id,zz){
            var a=this.list[id].geshu;//获取商品个数
            if(a==0){
                return
            }
            a--;
            this.list[id].geshu=a;
            var url='gs?id='+zz+'&geshu='+a;
            this.axios.get(url).then(result=>{
                // console.log(result);
            })
        },
        plus(id,zz){
            var a;
            var a=this.list[id].geshu;
            a++;
            this.list[id].geshu=a;

            var url='gs?id='+zz+'&geshu='+a;
            this.axios.get(url).then(result=>{
                // console.log(result);
            })
        }
    }
}
</script>

<style>
.mui-media-body span{
    line-height: 80px;
}
.mui-media-body span input{
    margin-top: 20px;
}
.di{
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-around;
    position: fixed;
    bottom: 50px;
    background: #e0e0e0;
    line-height: 50px;
}
.di button{
    background: #f00;
    width: 150%;
    height: 50px;
}
.mui-numbox span{
    display: flex;
    justify-content: center;
    line-height: 2;
}
.mui-numbox{
    margin-left: 50%!important;
    margin-right: 20px;
}
button.mui-btn.mui-btn-numbox-minus,button.mui-btn.mui-btn-numbox-plus{
    width: 20px!important;
    
}
div.mui-numbox{
    width: 82px!important;
    height: 32px!important
}
</style>



