import Vue from 'vue'
import Router from 'vue-router'
// 指定首页路径
import Home from "./components/tabbar/Home.vue"
// 招商加盟
import Procedure from "./components/home/Procedure.vue"
// 商品详情页
import GoodInfo from "./components/tabbar/GoodInfo.vue"
//新闻详情
import NewsInfo from "./components/tabbar/NewsInfo.vue"
// 购物车
import  gwc  from "./components/tabbar/gwc.vue";
// 个人中心
import  my  from "./components/home/my.vue";
Vue.use(Router)
//2.为Test组件配置访问路径 /Test
export default new Router({
  routes: [
    {path:'/',redirect:"Home"},//默认首页
    //为Home组件配置访问路径  /Home
    {path:'/Home',component:Home},
    //为Procedure组件配置访问路径/Procedure
    {path:'/Procedure',component:Procedure},
    //为GoodInfo组件配置访问路径/GoodInfo
    {path:'/GoodInfo',component:GoodInfo},
    //为NewsInfo组件配置访问路径/NewsInfo
    {path:'/NewsInfo',component:NewsInfo},
    // 购物车
    {path:'/gwc',component:gwc},
    // 个人中心
    {path:'/my',component:my}
  ]
})



