<template>
    <div class="app-newsinfo">
        <!--  -->
        <!-- <mt-header>
            <router-link to="" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
            <mt-button icon="more" slot="right">
            </mt-button>
        </mt-header> -->
        <!-- <h1>新闻详情</h1> -->
        <div class="mui-card" >
            <div class="mui-card-content">
				<div class="mui-card-content-inner">
                    <p style="color: #000;font-size:1.5rem">{{info.nname}}</p>
					<p>{{info.ntime}}</p>
				</div>
			</div>
			<div class="mui-card-header mui-card-media" :style="'height:40vw;background-image:url('+info.img+')'"></div>
            <div class="mui-card-content">
				<div class="mui-card-content-inner">
                    <p style="color: #333;">{{info.ntext}}</p>
				</div>
			</div>
			<div class="mui-card-footer">
				<a class="mui-card-link" @click="pageUp">上一页</a>
				<a class="mui-card-link" @click="nextPage">下一页</a>
			</div>
		</div>
            <!-- 底部 -->
        <div class="tea-foot">
            <hr style="bordr:1px solid #cccc">
            <ul>
                <li>
                    关于我们
                    <ul>
                        <li>公司简介</li>
                        <li>联系我们</li>   
                    </ul>
                </li>
                <li>
                    新闻资讯
                    <ul>
                        <li>业界资讯</li>
                        <li>公司动态</li>
                    </ul>
                </li>
                <li>
                    招商加盟
                    <ul>
                        <li>加盟流程</li>
                        <li>加盟优势</li>
                        <li>加盟政策</li>
                        <li>加入我们</li>
                    </ul>
                </li>
                <li>
                    产品展示
                    <ul>
                        <li>花果茶系列</li>
                        <li>绿茶系列</li>
                    </ul>
                </li>
            </ul>
        </div>
        <!-- 版权 -->
        <div class="footer">
        <p>Powered by MetInfo 6.2.0 ©2019-  mituo.cn</p> 
        </div>
    </div>
</template>
<script>
import {Toast} from 'mint-ui'//弹窗提示组件
export default {
    data(){
        return{
            nid:this.$route.query.nid,
            info:{}
        }
    },
    created(){
        this.findNewsInfo();
        // this.nextPage();
    },
    methods:{
         //查询新闻详细信息
        findNewsInfo(){
            //1.获取新闻编号
            var id=this.nid;
            //2.创建url保存请求地址
            var url="";
            url+="newsInfo?id="+id;
            // console.log(11);
            //3.发送ajax请求
            this.axios.get(url).then(result=>{
                //4.获取服务器返回数据保存 info
                this.info=result.data.data[0];
            })
        } ,
        pageUp(){// 上一页
                var nid=this.nid;
                if(this.nid==1){
                    //弹出提示
                    return Toast('没有了');
                }
                nid=parseFloat(nid)-1;
                this.nid=nid;
                this.findNewsInfo();
                setTimeout(() => {
                    //页面数据加载后100毫秒后地址栏参数改变     
                    this.$router.push({path:'/NewsInfo?nid='+nid});
                 }, 100);
        },
        nextPage(){//下一页
                if(this.nid==6){
                    return Toast('没有了');
                }
                var nid=this.nid;
                nid=parseFloat(nid)+1;
                this.nid=nid;
                this.findNewsInfo();
                setTimeout(() => {
                    //页面数据加载后100毫秒后地址栏参数改变     
                    this.$router.push({path:'/NewsInfo?nid='+nid});
                }, 100);
                
                
            }
    
    },
}
</script>
<style>
/* 底部 导航栏*/
   .tea-foot ul{
       display: flex;
       justify-content: space-around;
   }
   .tea-foot ul li ul{
       display: flex;
       flex-direction: column;
        font-size: 0.5rem;
        text-align: center;
        padding-top: 10px;
   }
   /* .tea-foot ul li ul li:hover{
       color: darkseagreen;
   } */
   .footer{
       width: 100%;
       height: 50px;
       border: 1px solid #ccc;
       text-align: center;
       line-height: 50px;
       background: #f5f5f5;
   }
</style>






