<template>
    <div>
    <div class="mui-content">
			<div class="title top-user">
                <div class="top-center">
                  <!-- 头像 -->
                    <div class="portrait">
                        <!-- <input type="file" accept="image/*" name="pic"> -->
                        <img src="../../img/1515835923108203.jpg">
                    </div>
                    <!-- 用户名 -->
                    <div class="name">{{this.mul}}</div>  
                </div>
                <div class="user" v-if="xian==1">
                    <div @click="logn">
                        <button>登录</button>
                    </div>
                    <div @click="reg">
                        <button>注册</button>
                    </div>
                </div>
			</div>
			<ul class="mui-table-view">
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						新消息通知
					</a>
				</li>
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						隐私
					</a>
				</li>
				<li class="mui-table-view-cell">
					<a class="mui-navigate-right">
						通用
					</a>
				</li>
			</ul>
			<ul class="mui-table-view" style="">
				<li class="mui-table-view-cell">
					<a id="about" class="mui-navigate-right">
						关于
					</a>
				</li>
			</ul>
			<ul v-if="xian==0" @click="tuichu" class="mui-table-view" style="margin-top: 25px;">
				<li class="mui-table-view-cell">
					<a style="text-align: center;color: #FF3B30;">
						退出登录
					</a>
				</li>
			</ul>
		</div>
        <div :class="active==1||active==2?'new-user':''">
            <div class="top-int">
                <!-- 登录 -->
                <div :class="active==1?'show':'hide'" class="logn1">
                    <!--  -->
                    <div class="logn2">登录</div>
                    <input type="text" id="uname" name="uname" placeholder="请输入账号">
                    <input type="password" id="upwd" name="upwd" placeholder="输入密码">
                    <div class="n-logn1">
                        <div @click="logn1"><button type="button" >登录</button></div>
                        <div @click="reg"><button type="button" >前往注册</button></div>
                    </div>
                </div>
                <!-- 注册 -->
                <div :class="active==2?'show':'hide'" class="reg1">
                    <div class="reg2">注册</div>
                    <input type="text" id="uname1" name="uname1" placeholder="请输入账号">
                    <input type="password" id="upwd1" name="upwd1" placeholder="请输入密码">
                    <input type="password" id="upwd2" name="upwd2" placeholder="请再次输入密码">
                    <input type="text" id="phone" name="phone" placeholder="请输入手机号">
                    <div class="n-logn">
                        <div @click="logn"><button type="button" >前往登录</button></div>
                        <div @click="reg1"><button type="button" >注册</button></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {Toast} from 'mint-ui'
export default {
    data(){
        return{
            active:0,
            mul:'',
            xian:1,
        }
    },
    created(){
        this.panduan();
    },
    methods:{
        tuichu(){
            localStorage.setItem('una','');//退出登录
            this.xian=1;
            this.panduan();
        },
       panduan(){//判断是否登录状态
           var ss=localStorage.getItem('una');
           this.mul=ss;
           if(ss.length>0){
               this.xian=0;//隐藏登录注册按钮
           }
           if(ss==undefined){
               this.mul='请登录';
           }
       },
        logn(){//点击显示登录
            this.active=1
        },
        reg(){//点击显示注册
            this.active=2
        },
        // 注册
        reg1(){
            var u=uname1.value;//获取注册用户名
            var up=upwd1.value;//获取注册密码
            var upw=upwd2.value;//获取注册再次输入的密码
            var pho=phone.value;//获取手机号
            if(up!==upw){//判断两次的密码是否相同
                return Toast('密码不相同请重新输入');
            }
            var url='reg?uname='+u+'&upwd='+up+'&phone='+pho;
            this.axios.get(url).then(result=>{//向数据库提交注册数据
                    Toast(result.data.msg);
            })
        },
        logn1(){
            var u=uname.value;//获取输入框用户名
            var p=upwd.value;//获取用户密码
            var url='dl?uname='+u+'&upwd='+p;
            this.axios.get(url).then(result=>{//向数据库提交登录数据
                if(result.data=='登陆成功'){
                    localStorage.setItem('una',u);
                    setTimeout(()=>{
                        this.$router.push({path:'/Home'});//登录成功后跳转到Home界面
                    },1000)
                }
                Toast(result.data);//提示框登录成功
            })
        }
    }
}
</script>
<style>
div.top-user{
    width: 100%; 
    height: 200px;
    background-color: #f5f5f5;
}
div.top-user .top-center{
    position: relative;
    left: 50%;
    margin-left: -49px;
    top: 20px;
}
div.top-user .top-center .name{
    width: 100px;
    text-align: center;
}
div.top-user .portrait{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
}
div.top-user .portrait img{
    width: 100%;
}
.user{
    display: flex;
    justify-content: center;
    margin-top: 40px;
}
/*登录注册框*/
.new-user{
    width: 100%;
    height: 100%;    
    position: absolute;
    top: 40px;
    background:#f5f5f5;
}
.top-int{
    margin-top: 50px
}
/*登录注册按钮*/ 
.n-logn1{
    display: flex; 
     justify-content:space-between;
    width: 80%;
    margin-left: 10%;
}
.n-logn1>div:nth-child(1){
     width: 60%;
}
.n-logn1>div:nth-child(1)>button{
    width: 100%;
    background: rgb(0, 183, 255);
} 
.n-logn{
    display: flex; 
     justify-content:space-between;
    width: 80%;
    margin-left: 10%;
}
.n-logn>div:nth-child(2){
    width: 60%;
}
.n-logn>div:nth-child(2)>button{
    width: 100%;
    background: rgb(0, 183, 255);;
} 
/*登录注册 头部字体*/ 
.logn2,.reg2{
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 20px;
}
/*输入框*/ 
.logn1>input,.reg1>input{
    width: 80%!important;
    height: 50px!important;
    border: 1px solid #ccc!important;
    margin-left: 10%;
    
}
</style>



