<template>
    <div class="app-home">
        <!-- <h1>首页</h1> -->
        <!-- 1.顶部导航条 mint-ui-->
        <!-- <mt-header fixed title="茶业">
            <img src="vue_app_00\src\img\1515997569.png" alt="">
        </mt-header> -->
       <!-- <mt-header></mt-header> -->
        <!-- 2.轮播图  mint-ui-->
        <mt-swipe>
            <!-- v-for="item of navlist 循环获取显示 -->
            <mt-swipe-item v-for="item of navlist">
                <img :src="item.img_url">
            </mt-swipe-item>
        </mt-swipe>
        <!-- 3.九宫格 mui-->
        <ul class="mui-table-view mui-grid-view mui-grid-9">
            <li class="cent">
                <p>招商加盟</p>
                <h5>join us</h5>
            </li>
		    <li class="mui-table-view-cell mui-media mui-col-xs-6 mui-col-sm-3" v-for="item of gridlist">
                <a href="#">
		           <img :src="item.img_url" alt="" @click.prevent="jump" :data-id="item.id">
		            <div class="mui-media-body">{{item.title}}</div>
                </a>
            </li>
        </ul>
        <!-- 4.产品展示 -->
         <div class="app-good">
             <ul class="cent">
                 <li >
                     <p>商品展示</p>
                     <h5>product</h5>
                 </li>
                 
             </ul>
            <ul class="click">
                <li @click="change1()" :class="active==1?'fn2':''">全部</li>
                <li @click="change2()" :class="active==2?'fn2':''">花果系列</li>
                <li @click="change3()" :class="active==3?'fn2':''">绿茶系列</li>
            </ul> 
            
        </div>
        <div class="app-goodlist">
        <!-- <h1>商品列表 </h1> -->
        <!-- 商品详细信息  start -->
            <div class="goods-item" v-for="(item,index) of list" v-if="index%2!=1">
                <img :src="item.img_url" @click.prevent="jumpInfo(item.uid)">
                <h4>{{item.uname}}</h4>
                <div class="info">
                    <span class="now">¥{{item.pice}}.00</span>
                </div>
            </div>
            <!-- 商品详细信息  end -->
             
        </div>
        <!-- 新闻资讯 -->
        <div class="app-newslist">
            <div class="homeNew">
                <p>新闻资讯</p>
                <h5>News</h5>
            </div>
            <ul class="mui-table-view">
                <li class="mui-table-view-cell mui-media" v-for="item of list1">
                    <router-link :to="'/NewsInfo?nid='+item.id">
                        <img class="mui-media-object mui-pull-left nimg" :src="item.tu">
                        <div class="mui-media-body ndiv" style=" text-overflow:ellipsis;">
                            {{item.title}}
                            <p style=" text-overflow:ellipsis;">{{item.ntext}}</p> 
                            <p class='mui-ellipsis'>
                                <span>{{item.ctime}}</span>
                                <span>点击{{item.point}}次</span>
                            </p> 
                        </div>
                    </router-link>
                </li>
            </ul>
        <!-- 加载更多按钮 -->
    <!-- 添加按钮 加载更多 -->
    <!-- <mt-button size="large" type="primary" @click="loadMore">加载更多</mt-button> -->
    <!-- 点击按钮显示一页数据 一直追加 -->
        </div>
            <!-- 底部 -->
        <div class="tea-foot">
            <hr style="bordr:1px solid #cccc">
            <ul>
                <li>
                    关于我们
                    <ul>
                        <li>公司简介</li>
                        <li>联系我们</li>   
                    </ul>
                </li>
                <li>
                    新闻资讯
                    <ul>
                        <li>业界资讯</li>
                        <li>公司动态</li>
                    </ul>
                </li>
                <li>
                    招商加盟
                    <ul>
                        <li>加盟流程</li>
                        <li>加盟优势</li>
                        <li>加盟政策</li>
                        <li>加入我们</li>
                    </ul>
                </li>
                <li>
                    产品展示
                    <ul>
                        <li>花果茶系列</li>
                        <li>绿茶系列</li>
                    </ul>
                </li>
            </ul>
        </div>
        <!-- 版权 -->
        <div class="footer">
        <p>Powered by MetInfo 6.2.0 ©2019-  mituo.cn</p> 
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            navlist:[],//轮播列表
            gridlist:[],//九宫格
            list:[],//模板中显示商品列表
            pno:0,//当前页码
            active:1,//默认选中全部商品
            err0:[],//存放全部商品
            err1:[],//存放花果系列
            err2:[],//存放绿茶系列
            list1:[],//返回新闻列表
            pno:0//页码
        }
    },
    created(){
        this.handleImage();//轮播图
        this.handleGrid();//九宫格
        this.loadMore();//商品列表
        this.loadMore1();
    },
    methods:{
        jumpInfo(e){
            this.$router.push("/GoodInfo?id="+e);
        },
        jump(e){
            var id = e.target.dataset.id;
            var path = "/";
            if(id==1){
                path = "/Procedure?lid=1";
            }else if(id==2){
                path = "/Procedure?lid=2";
            }else if(id==3){
                path = "/Procedure?lid=3";
            }else if(id==4){
                path = "/Procedure?lid=4";
            }
            else{
                path = "/";
            }
            this.$router.push(path);
            
            // console.log(id);
            // console.log(123);
            // this.$router.push("/GoodList");
        },
        handleGrid(){
            //1.发送ajax获取九宫格数据
            var url="grid";
            this.axios.get(url).then(result=>{
                //2.获取返回数据
                //console.log(result.data.data);
                //3.保存gridlist
                this.gridlist=result.data;
            })
          
        },
        // 加载轮播图的数据
        handleImage(){
            //1.发送ajax获取轮播数据
            var url="imglist";
            this.axios.get(url).then(result=>{
                //2.获取返回结果
            //    console.log(result.data.data);

                //3.保存data属性中
                this.navlist=result.data.data;
            })
            
        },
        //加载下一页数据并且额将数据显示在模块上
        //商品展示
        loadMore(){
            // console.log(11111);
            //0.获取当前页码
            this.list="";
            //1.创建变量url请求地址
            var url="getProducts";
            //2.发送ajax请求
            this.axios.get(url).then(result=>{
                //如果是下面这一行代码 就是下页的效果
                //this.list=result.data.data;
                //3.获取服务器返回 结果 追加list
                //这两行代码是 同屏向下加载
                var rows=this.err0.concat(result.data.data);//获取全部商品
                this.err0=rows;//全部商品
                // console.log(this.err0);
                this.err1=rows.slice(0,8);//截取的前四个商品
                this.err2=rows.slice(8,16);//截取的最后四个商品
                this.list=this.err0;//默认的全部商品
                // console.log(this.list);
            })
            //3.获取服务器返回结果保存data
        },
        change1(){//全部商品
            this.list=this.err0;
            this.active=1
        },
        change2(){//花果系列
            this.list=this.err1;
            this.active=2
        },
        change3(){//绿茶系列
            this.list=this.err2;
             this.active=3
        },
        //新闻资讯
         loadMore1(){
            //1.当前页数加+1       pno
            this.pno++;
             //2.创建变量url请求地址
            var url="newsList?pno=0"+this.pno;
             //3.发送ajax请求 //4.获取服务器数据
            this.axios.get(url).then(result=>{
                console.log(result);
                //点击下一页
            //    this.list1=result.data.data;
                //在原页的基础上加载下一页
                
                var rows1=this.list1.concat(result.data.data);
                //5.将获取数据保存data list:[]1
                this.list1=rows1; 
            });
            
            
             

        }
    }
}
</script>
<style>
/*轮播*/
.app-home .mint-swipe{
    height: 145px;
    background: #000;
}
.app-home .mint-swipe img{
    width:375px;
    height: 145px;
    
}
/*圆点*/
.mint-swipe-indicators{
    background-color: rgba(255, 255, 255, 0)
}
.mint-swipe-indicator{
    opacity: 0.8!important;
}

/*九宫格图片宽度*/
.mui-table-view{background:#f5f5f5;}
.app-home .mui-grid-9 .cent{
    text-align: center;
    padding-top: 41px;
    padding-bottom: 20px;
    
}
.app-home .mui-grid-9 .cent p{
    font-size:1.7rem;
}
.app-home .mui-grid-9 img{
    width: 60px;
    height: 60px;
}
/*九宫格  背景颜色 白色*/
.app-home .mui-grid-view.mui-grid-9{
    background: #ffffff;
}
/*商品展示列表*/
.app-good ul{
    display: flex;
    justify-content:flex-start;
    margin-left: 20px;
    margin-left: 0;
    margin-bottom: 0px;
    margin-top: 20px;
}
.app-good .cent li{
    /* flex-wrap: wrap; */
    /* justify-content: center; */
    text-align: center;
     margin-left: 34%;
     margin-top: 30px;
    }
.app-good .cent li p{
    font-size: 1.7rem;
   margin-bottom: 20px;
    
}
.app-good .click li{
    margin-left: 10px;
    padding: 5px;
    /* background: #71a902; */
    border: 1px solid #ccc;
    border-radius: 10%;
    color: #110000;
    
}
.fn2{background: #71a902;}
/* 外层父元素 */
    .app-goodlist{
        /* 弹性布局 */
        display: flex;
        /* 子元素换行 */
        flex-wrap:wrap;
        /* 两端对齐 */
        justify-content: space-between;
        padding: 4px; 
        text-align: center;
    }
    /* 商品信息 */
    .app-goodlist .goods-item{
        width:49%;
        border: 1px solid #ccc;
        margin: 2px 0;
        padding: 2px;
        display:flex;
        /* 排列方式：按列 */
        flex-direction: column;
        /* 最小高度 */
        min-height:245px;
        background: #f5f5f5;
    }
    /* 商品图片 */
    .app-goodlist .goods-item img{
        width: 100%;
    }
    /* 商品名称 */
    /* 商品价格 */
    .mt-b{
        background: #000;
    }
    .mint-button--primary{
        background: #71a902!important;
    }
    /* 新闻信息 */
    .app-newslist,.mui-table-view{background: #efeff4;}
    .homeNew{
        text-align: center;
        padding-top: 30px;
    }
    .homeNew p{font-size: 1.7rem;margin-bottom: 10px;}
    .homeNew h5{margin-bottom: 20px;}
    /* 修改 新闻内容 时间 点击 次数两端对齐 */
.app-newslist .mui-ellipsis{
    display: flex;
    font-size: 12px;
    color:#226aff;
    justify-content: space-between;
}
.app-newslist .mui-ellipsis .nimg{
    width: 30%;
}
.app-newslist .mui-ellipsis .ndiv{
    width: 70%;
}
.mui-table-view .mui-media-object{
    max-width: 200px!important;
    height: 80px!important;
}
/* 底部 导航栏*/
   .tea-foot ul{
       display: flex;
       justify-content: space-around;
   }
   .tea-foot ul li ul{
       display: flex;
       flex-direction: column;
        font-size: 0.5rem;
        text-align: center;
        padding-top: 10px;
   }
   /* .tea-foot ul li ul li:hover{
       color: darkseagreen;
   } */
   .footer{
       width: 100%;
       height: 50px;
       border: 1px solid #ccc;
       text-align: center;
       line-height: 50px;
       background: #f5f5f5;
   }
</style>



