<template>
  <div class="app-goodinfo">
     <!-- <h1>商品详细</h1> -->
     <div><img src="../../img/1515824208.jpg" style="width:100%" alt=""></div>
     <div v-if="info" class="mui-card">
                <img :src="info[0].img_url" alt="" style="">
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
						<p style="font-size:1.5rem">{{info[0].uname}}</p>
						<p style="color: #333;">{{info[0].introduce}}</p>
					</div>
				</div>
                <div class="mui-card-content">
					<div class="mui-card-content-inner price">
						¥{{info[active-1].pice}}.00
					</div>
                    
				</div>
				<div class="mui-card-content">
                    <div class="mui-card-content-inner">
                      <p>净含量</p>
                      <ul style="margin-bottom:0px">
                        <li @click="fn1()" :class="active==1?'fn1':''">{{info[0].gram}}g</li>
                        <li @click="fn2()" :class="active==2?'fn1':''">{{info[1].gram}}g</li>
                      </ul>  
                    </div>
                </div>
                <p style="margin-left:16px">数量</p>
                <div class="mui-numbox" data-numbox-min='1' data-numbox-max='9' style="margin-left:15px;">				
                    <button @click="minus()" class="mui-btn mui-btn-numbox-minus" type="button">-</button>
					<span>{{tmp}}</span>
					<button @click="plus()" class="mui-btn mui-btn-numbox-plus" type="button">+</button>
				</div>
                <span @click="gwc(info1[0].id)" class="splus">加入购物车</span>
                <p style="margin-left:16px;margin-top:10px">库存99件</p>
                <!-- <div class="mui-card-footer">
                    <mt-button type="danger" size="small" @click="addcart">加入购物车</mt-button>
                </div> -->
                <hr>
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                      <p style="color:#b3d668;font-size:1rem">详细信息：</p>
                      
                      <div>
                        <p>产品名：{{info[0].uname}}</p>
                        <p>建议零售价：¥{{info[0].pice}}.00</p>
                        <p>质量等级：特级</p>
                        <p>净含量：{{info[0].gram}}g</p>
                      </div>  
                    </div>
                </div>
                <img v-if="info1" :src="info1[0].dimg" alt="" style="width:100%">
			</div>
    <!-- 底部 -->
    <div class="tea-foot">
        <hr style="bordr:1px solid #cccc">
        <ul>
            <li>
                关于我们
                <ul>
                    <li>公司简介</li>
                    <li>联系我们</li>   
                </ul>
            </li>
            <li>
                新闻资讯
                <ul>
                    <li>业界资讯</li>
                    <li>公司动态</li>
                </ul>
            </li>
            <li>
                招商加盟
                <ul>
                    <li>加盟流程</li>
                    <li>加盟优势</li>
                    <li>加盟政策</li>
                    <li>加入我们</li>
                </ul>
            </li>
            <li>
                产品展示
                <ul>
                    <li>花果茶系列</li>
                    <li>绿茶系列</li>
                </ul>
            </li>
        </ul>
    </div>
    <!-- 版权 -->
    <div class="footer">
       <p>Powered by MetInfo 6.2.0 ©2019-  mituo.cn</p> 
    </div>
  </div>
</template>
<script>
import {Toast} from 'mint-ui'
export default {
    data(){
        return{
            id:this.$route.query.id,
            info:null,//显示商品对象
            info1:null,
            active:1,
            tmp:1,
        }
        
    },
    mounted(){
            this.findProduct();
    },
    methods:{
        //当组件创建成功后加载当前商品信息
      findProduct(){
          //1:获取商品lid
          //2:发送ajax请求
        //   var list={};
          var url = "findProduct?uid="+this.id;
          this.axios.get(url).then(result=>{
            //console.log(result);10:16
            this.info = result.data.data;
          });
          var url = "findProduct1?uid="+this.id;
          this.axios.get(url).then(result=>{
            this.info1 = result.data;
          });
      },
      fn1(){
          this.active=1;  
      },
      fn2(){
          this.active=2;  
      },
        gwc(id){
            var ss=localStorage.getItem('una');
            
            if(!ss){//判断现在是否是登录状态
                return Toast("请登录")
            }
            var shu;
            if(this.active==1){
                shu=0;
            }
            if(this.active==2){
                shu=1;
            }
            var nname=this.info[shu].uname;
            var img=this.info[shu].img_url;
            var pice=this.info[shu].pice;
            var gram=this.info[shu].gram;
            var tmp=this.tmp;
            var url='gwc?uname='+ss+'&nname='+nname+'&img='+img+'&pice='+pice+'&gram='+gram+'&tmp='+tmp;
           
            this.axios.get(url).then(result=>{
                Toast("添加成功")
            })
        },
        minus(){
            if(this.tmp==1){
                return;
            }
            this.tmp=this.tmp-1;
        },
        plus(){
            this.tmp=this.tmp+1;
        }
    }
}
</script>
<style>

.mui-card{
    margin: 0!important;
}
.mui-card-content .price{
    background: #ccc;
    color: #f96868;
    font-size: 2rem;
    
}
  .mui-card-content ul{
      display: flex;
  }  
  .mui-card-content ul li{
      border: 1px solid #ccc;
      padding: 5px;
      /* margin-left: 18px; */
  }
  .mui-card-content ul li:last-child{
      margin-left: 20px;
  }
  /* 点击重量添加颜色 */
  .fn1{
    color: red;
    border:1px solid #f96868;
    background: #fff;
}
/* 底部 导航栏*/
   .tea-foot ul{
       display: flex;
       justify-content: space-around;
   }
   .tea-foot ul li ul{
       display: flex;
       flex-direction: column;
        font-size: 0.5rem;
        text-align: center;
        padding-top: 10px;
   }
   /* .tea-foot ul li ul li:hover{
       color: darkseagreen;
   } */
   .footer{
       width: 100%;
       height: 50px;
       border: 1px solid #ccc;
       text-align: center;
       line-height: 50px;
       background: #f5f5f5;
   }
</style>



