        <li @click="fn1()" :class="active==1?'fn':''">加盟流程</li>
        <li @click="fn2()" :class="active==2?'fn':''">加盟优势</li>
        <li @click="fn3()" :class="active==3?'fn':''">加盟政策</li>
        <li @click="fn4()" :class="active==4?'fn':''">加入我们</li>
    </ul> 
