            <li>2.向总部索取相关资料，或以电话、传真、网上留言等方式向总部咨询合作;</li>
            <li>3.到总部所在地进行项目考察，并与总部工作人员进行业务交流确定投资规模等；</li>
            <li>4.双方确认考察结果无争议，正式签订代理合同；</li>
            <li>5.进行相应培训(施工、门店话术等）;</li>
            <li>6.领取授权书、开业赠品、全套施工工具、物流配送完毕，人员到位，准备开业。</li>
        </ul>
        
