            <li>百年中华老字号；2008年奥运会独家运营媒体村中国茶艺室；2010年世博会特许生产商及零售商；2014年APEC制定礼品；四川省知名品牌奖、四川省十大商业品牌奖</li>
        </ul>
        <ul class="chan">
            <li><img src="../../img/1500866110962061.png" alt=""></li>
            <li>产品优势</li>
            <li>“大花茶”概念，全品类产品；茉莉花茶窖制技巧列为国家非物质文化遗产；产品不断创新—兰花花茶、茶月饼、茶爽等；业内率先通过QS认证、ISO等认证体系。</li>
        </ul>
    </div>
    <!-- 加盟政策 -->
    <div class="d3" :class="active==3?'show':'hide'">
        <ul class="">
            <li><h4>意向加盟</h4></li>
            <li><img src="../../img/1500964061130050.png" alt=""><span>标准化规范经营</span></li>
            <li><img src="../../img/1500964072121435.png" alt=""><span>具有经济管理能力</span></li>
            <li><img src="../../img/1500964082111374.png" alt=""><span>具有一定经济实力</span></li>
        </ul>
        <ul class="">
            <li><h4>意向店址</h4></li>
            <li><img src="../../img/1500964092552528.png" alt=""><span>有充足的客流量</span></li>
            <li><img src="../../img/1500964104626800.png" alt=""><span>有独立店面</span></li>
