    <!-- 版权 -->
    <div>
        
    </div>
</div>
</template>
<script>
export default {
    data(){
        return{
            //判断点击加盟页四个选项
            active:1,
            lid:0,
            lid:this.$route.query.lid
        }
    },
    created(){
        this.active=document.URL.split("=")[1];
        
    },
    methods:{
        fn1(){
            this.active=1;  
        },
        fn2(){
             this.active=2;

        },
        fn3(){
             this.active=3; 
        },
        fn4(){
             this.active=4; 
        },
    }
}
</script>
<style>
    .top-img{
        height: 100px!important;
    }
    .hide{
        display: none !important;
    }
    /* 点击导航栏添加颜色 */
    .fn{
        color: limegreen;
         padding: 2px;
        border: 1px solid limegreen;
        border-radius: 8%;
        }
    div.procedure{
        background: #eeecec;
    }
    /* 加盟流程 */
    ul{list-style: none;padding: 0;}
    .league{
        display: flex;
        justify-content:space-around;
        font-weight: 600;
        margin-bottom: 0px;
        margin-top: 0;
        padding: 10px;
        background: #ccc;
    }
    .league li{
        padding: 5px;
        /* border: 1px solid #fff; */
    }
    .league-txt{
