    }
    /* 加盟优势 */
    .d2{
        display: flex;
        justify-content: space-between;
        font-size: 0.9rem;
        line-height: 1.5rem;
        padding-left: 8px;
        padding-right: 10px;
        }
    .d2 ul{
        border: 1px solid #cccccc;
