   .d3{
       display: flex;
       justify-content: space-around;
   }
   .d3 ul li{
       border: 1px solid #cccccc;
       padding: 5px;
       border-top: 0px;
   }
   .d3 ul li:first-child{
       border-top:1px solid #cccccc; 
   }
   .d3 ul li span{
       padding-left: 10px;
       font-size: 0.9rem;
   }
   /* 加入我们 */
   .d4 input{
       width: 200px;
       margin: 0px 10px 0px 10px;
   }
   /* 底部 导航栏*/
   .tea-foot ul{
       display: flex;
       justify-content: space-around;
   }
   .tea-foot ul li ul{
       display: flex;
       flex-direction: column;
        font-size: 0.5rem;
        text-align: center;
        padding-top: 10px;
   }
   .tea-foot ul li ul li:hover{
       color: darkseagreen;
   }
</style>



